import React, { useEffect } from "react"
import { useForm } from "react-hook-form"
import { useDispatch } from "react-redux"
import { address, updateCustomerAddress } from "../../../store/customer"
import { UIScreenTitles } from "../../../store/ui"
import { FieldSet, Form, Input, Label, LabelText, Legend } from "../../common/forms"
interface props {
  onValidChange: (isValid: boolean, screen: string) => void
}

export default function AddressForm(props: props) {
  const dispatch = useDispatch()
  const { register, getValues, triggerValidation } = useForm<address>()
  
  //todo - fix
  useEffect(() => {
    props.onValidChange(false, UIScreenTitles.YOUR_ADDRESS)
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleChange = async () => {
    dispatch(updateCustomerAddress(getValues()))
    const result = await triggerValidation()
    props.onValidChange(result, UIScreenTitles.YOUR_ADDRESS)
  }

  return (
    <React.Fragment>
      <Form onChange={handleChange}>
        <FieldSet>
          <Legend>Enter your address</Legend>
          <Label htmlFor="address-line-1">
            <LabelText>Line 1</LabelText>
            <Input
              id="address-line-1"
              name="address-line-1"
              placeholder="11 Smith St"
              ref={register({ required: true })}
            />
          </Label>

          <Label htmlFor="address-line-2">
            <LabelText>Line 2</LabelText>
            <Input
              id="address-line-2"
              name="address-line-2"
              placeholder=""
              ref={register}
            />
          </Label>

          <Label htmlFor="city">
            <LabelText>City</LabelText>
            <Input
              id="city"
              name="city"
              placeholder="Melbourne"
              ref={register({ required: true })}
            />
          </Label>

          <Label htmlFor="country">
            <LabelText>Country</LabelText>
            <Input
              id="country"
              name="country"
              defaultValue="Australia"
              placeholder="Australia"
              ref={register({
                required: true,
              })}
            />
          </Label>

          <Label htmlFor="state">
            <LabelText>State</LabelText>
            <Input
              id="state "
              name="state"
              placeholder="VIC"
              ref={register({ required: true })}
            />
          </Label>
        </FieldSet>
      </Form>
    </React.Fragment>
  )
}
