import React, { useRef } from 'react'
import { animated, config, useSprings } from 'react-spring'
import { useMedia } from 'react-use'
import StepTitle from '../StepTitle'
import { StepTitleContainer } from './style'

interface StepTitlesProps {
  titles: string[]
  activeStep: string
}
export default function StepTitles(props: StepTitlesProps) {
  const ref = useRef<HTMLDivElement>(null)
  const isTabletUp = useMedia('(max-width: 768px)')
  const activeIndex = props.titles.findIndex((s) => s === props.activeStep)

  const springs = useSprings(
    props.titles.length,
    props.titles.map((item, idx) => {
      const extra = idx > activeIndex ? 180 : 0
      const negOffset = isTabletUp ? activeIndex * 50 : 0
      return {
        to: {
          transform: `translate3d(${idx * 50 + extra - negOffset}px, 0, 0)`,
        },
        config: config.stiff,
      }
    }),
  )

  const stepTitles = springs.map((value, idx, arrayVals) => {
    return (
      <animated.div style={{ position: 'absolute', willChange: 'transform', ...value }} key={idx}>
        <StepTitle number={idx + 1} title={props.titles[idx]} isActive={props.titles[idx] === props.activeStep} />
      </animated.div>
    )
  })

  return <StepTitleContainer ref={ref}>{stepTitles}</StepTitleContainer>
}
