import { EntityState } from '@reduxjs/toolkit';
import { createApi } from '@reduxjs/toolkit/query/react';
import { gql, GraphQLClient } from 'graphql-request';
import { normalize } from '../../utils/utils';
import { Product } from '../catalog';
import { Accessory } from '../entities';

interface response {
  products: Product[]
}

const getProductsGql = gql`{
  products(sort: "rrp") {
    data {
      id
      attributes {
        name
        short_description
        description
        rrp
        featured_image {
          data{
            id
            attributes{
              url
              width
              height
            }
          }
        }
        models(sort: "rrp") {
          data{
            id
            attributes {
              name
              short_description
              description
              rrp
              included_accessories{
                data {
                  id
                }
              }
              featured_image{
                data {
                  id 
                  attributes{
                    url
                  }
                }
              }
            }
          }    
        }
      }     
    }
  }
}
`;
// accessories( filters: {option_for: {name: { eq: "All Roada" }}}) {
const getAccessoriesGql = gql`{
  
  accessories {
    data {
      id
      attributes{
        name
        short_description
        description
        rrp
        featured_image {
          data{
            attributes{
              url    
            }
          }
        }
        accessory_categories{
          data{
            id
            attributes{
              name      
            }
          }
        }
        option_for {
          data{
            id 
            attributes{
              name
            }
          }
        }
        included_in {
          data {
            id  
          }
        }        
    }
  }   
}
}
`
const graphqlBaseQuery =
  ({ baseUrl }: { baseUrl: string }) =>
    async ({ body }: { body: string }) => {
      try {
        const graphQLClient = new GraphQLClient(baseUrl, {
          // headers: {
          //   authorization: 'Bearer MY_TOKEN',
          // },
        })
        const result = await graphQLClient.request(body)
        return { data: result }
      } catch (error) {
        // if (error instanceof ClientError) {
        //   return { error: { status: error.response.status, data: error } }
        // }
        return { error: { status: 500, data: error } }
      }
    }
    
// https://www.reddit.com/r/reduxjs/comments/pd93b6/rtk_query/
export const strapiApi = createApi({
  reducerPath: 'strapiApi',
  baseQuery: graphqlBaseQuery({ baseUrl: process.env.REACT_APP_GRAPHQL_URL+"" }),
  endpoints: (builder) => ({
    getAllProducts: builder.query<response, void>({
      query: () => ({
        body: getProductsGql
      }),
    }),
    getOptionalAccessories: builder.query<EntityState<Accessory>, void>({
      query: () => ({
        body: getAccessoriesGql
      }),
      async onQueryStarted( arg, {dispatch, queryFulfilled}) {
        try {
          const { data } = await queryFulfilled;
          console.log(data);
        } catch (err){
          console.log('err in query')
        }
      }, 
      transformResponse: (response: any) => {
        return normalize(response.accessories)
      }
    }),
  }),
})
export const { useGetAllProductsQuery, useGetOptionalAccessoriesQuery } = strapiApi