import { createGlobalStyle } from 'styled-components/macro'

const size = {
  mobileS: '320px',
  mobileM: '375px',
  mobileL: '425px',
  tablet: '768px',
  laptop: '1024px',
  laptopL: '1440px',
  desktop: '1920px',
}

export const theme = {
  device: {
    mobileS: `(min-width: ${size.mobileS})`,
    mobileM: `(min-width: ${size.mobileM})`,
    mobileL: `(min-width: ${size.mobileL})`,
    tablet: `(min-width: ${size.tablet})`,
    laptop: `(min-width: ${size.laptop})`,
    laptopL: `(min-width: ${size.laptopL})`,
    desktop: `(min-width: ${size.desktop})`,
  },
}

export const GlobalStyles = createGlobalStyle`
  :root {
    --text: #373a47;
    --black: #373a47;
    --black-1: #282a33;
    --accent-dark: #e41212;
    --accent-dark-trans: #e4121255;
    --accent-dark-hover: #ff1d1d;
    --input-outline-hover: #ff1d1d;
    --white: #ffffff;
    --grey-1: #f6f7f9;
    --grey-1x: #e6e7e9;
    --grey-2: #dde0e6;
    --grey-3: #a5a9be;
    --grey-4: #9599ae;
    --grey-5: #7a7f97;
    --grey-6: #5a5f77;
    --grey-7: #4a4f67;
    --inverse-text: #dde0e6;
    --disabled: #cacaca;
    --box-shadow: #a5a9beaa;
    --confirm: green;
    --warning: red;
    --confirm-bg: #dbf7db;
    --heading-font: "Nunito", sans-serif;
    --body-font: "Nunito", sans-serif;
    --product-select-gutter: 10px;
    --product-min-width: 200px;
    --product-select-rows: 2;
    --sidebar-width: 33%;
    --header-height: 60px;
    --footer-height: 120px;
    --product-select-height: 312px;
    --product-select-height-max: 430px;
    --container-max-width: 1200px;
    --accessory-button-height: 58px;
    @media (min-width: 1200px) {
      :root {
        --accessory-button-height: 58px;
        --product-select-gutter: 16px;
      }
    }
  }
  ::-webkit-scrollbar {
  width: 23px;
  }

  ::-webkit-scrollbar-track {
      background-color: transparent;
  }

  ::-webkit-scrollbar-thumb {
      background-color: var(--grey-4);
      border-radius: 20px;
      border: 10px solid transparent;
      background-clip: content-box;
  }

  ::-webkit-scrollbar-thumb:hover {
      background-color: #a8bbbf;
  }
`
