import { animated } from 'react-spring'
import styled from 'styled-components/macro'

interface theProps {
  $isActive?: boolean
}

export const AppWrapper = styled.div`
    font-family: var(--heading-font);
    
    color: var(--black);
    min-height: 100vh;
    background-color: white;

    @media ${p => p.theme.device.tablet}{
      background-color: var(--grey-1);
    }
 

    &&::placeholder {
        color: var(--grey-3);
    }

    && p{
        margin: 16px 0;
        font: normal 15px/20px var(--heading-font);
    }

    && img {
        max-width: 100%;
    }

`

export const AppHeader = styled.header`
    padding: 10px;
    width: 100%;
    align-items: flex-end;
    justify-content: space-between;
    margin-bottom: 10px;
    display: flex;
    position:relative;
    overflow: hidden;
    flex-direction: column;
    border-bottom: 1px solid var(--grey-2);
    
    @media ${p => p.theme.device.tablet}{
      flex-direction: row;
      border-bottom: none;
      padding: 0;
    }
`
export const Container = styled.div`
  display: block;
  max-width: var(--container-max-width);
  margin: 0 auto;
  overflow: hidden;
`
export const ScreenSection = styled.div`
  position: relative;

  @media ${p => p.theme.device.tablet}{
    height: calc(
      var(--product-select-rows) * var(--product-select-gutter) +
        var(--product-select-rows) * var(--product-select-height)
    );
  }
`

export const AnimatedScreen = styled(animated.div)<theProps>`
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  right: 0;
  bottom: 0;
  will-change: transform;
  position: ${p => p.$isActive ? 'static' : 'absolute'};
  pointer-events: ${p => p.$isActive ? 'all' : 'none'};
`