import styled, { css } from 'styled-components/macro'

interface ButtonProps {
  primary?: boolean
  secondary?: boolean
  outline?: boolean
  disabled?: boolean
}

export const Screen = styled.div`
  display: flex;
  background-color: white;
  padding: 2%;
  justify-content: space-between;
`
const fontFamily = '"Nunito", sans-serif'
export const Heading1 = styled.h1`
  font: 700 2em/1.2em ${fontFamily};
  margin: 0;
`
export const Heading2 = styled.h2`
  font: 700 1.5em/1.2em ${fontFamily};
  margin: 0;
`
export const Heading3 = styled.h3`
  font: 400 1.3em/1.2em ${fontFamily};
  margin: 0;
`
export const Heading4 = styled.h4`
  font: 400 1em/1.2em ${fontFamily};
  margin: 0;
`
export const Heading5 = styled.h5`
  font: 400 0.83em/1.2em ${fontFamily};
  margin: 0;
`
export const Heading6 = styled.h6`
  font: 400 0.7em/1.2em ${fontFamily};
  margin: 0;
`

export const Button = styled.button<ButtonProps>`
  display: flex;
  padding: 10px 20px;
  display: inline-flex;
  align-items: center;
  text-decoration: none;
  font-weight: 700;
  font-size: 16px;
  font-family: 'Nunito', sans-serif;

  &:hover {
    background-color: var(--accent-dark-hover);
    border: 1px solid var(--accent-dark);
    cursor: pointer;
  }

  ${({ secondary, disabled }) =>
    !secondary &&
    !disabled &&
    css`
      border: 1px solid var(--accent-dark);
      background-color: var(--accent-dark);
      color: var(--grey-1);
    `}

  ${({ secondary, disabled }) =>
    secondary &&
    !disabled &&
    css`
      border: 1px solid var(--grey-3);
      background-color: var(--grey-3);
      color: var(--grey-1);
    `}

    ${({ disabled }) =>
    disabled &&
    css`
      border: 1px solid var(--disabled);
      background-color: var(--disabled);
      cursor: auto;
      pointer-events: none;
    `}
`
export const TextButton = styled.button`
  background-color: transparent;
  display: inline;
  border: 0;
  padding: 0;
  font-weight: 600;
  font-size: 15px;
  color: var(--accent-dark);
  text-decoration: none;
  &:hover {
    color: var(--accent-dark-hover);
    cursor: pointer;
  }
`
