import React, { useState } from "react"
import { VscCreditCard, VscMail } from "react-icons/vsc"
import { useSelector } from "react-redux"
import {
  selectBuildAccessories,
  selectBuildCost, selectBuildImage, selectBuildModel,
  selectBuildProduct,
  selectFullBuild
} from "../../../store/build"
import { selectCustomerDetails } from "../../../store/customer"
import { formatCurrency } from "../../../utils/utils"
import { Button, Heading1, Heading3, Heading4 } from "../../common/style"
import { AccentText, ButtonGroup, Price, ProductImage, ProductImageWrapper, Wrapper } from './style'

export default function ScreenThankYou() {
  const product = useSelector(selectBuildProduct)
  const model = useSelector(selectBuildModel)
  const accessories = useSelector(selectBuildAccessories)  
  const image = useSelector(selectBuildImage)

  const [emailSent, setEmailSent] = useState(false);
  const allDetails = {
    ...useSelector(selectFullBuild), 
    customer: useSelector(selectCustomerDetails)
  };
  const sendMail = () =>{

    setEmailSent(true);
    if(window.szReactPlugin && emailSent===false){
      fetch(window.szReactPlugin?.email_endpoint, {
        method: "POST",
        headers: {
          'Accept': 'application/json, text/plain, */*',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(allDetails)
      })
        .then((response) => response.json())
        .then((data) => {
          if (data) {
            console.log(data)
          }
        })
        .catch((error) => {
          console.error(error);
        });
    }
  }

  return (
    <Wrapper>
      <Heading3>Thank you for your enquiry</Heading3>
      <Heading1><AccentText>{product?.name}</AccentText> {model?.name}</Heading1>
      {accessories.length > 0 && <Heading4 className="thankyou-accessories">With {accessories.length} Accessories</Heading4>}
      <ProductImageWrapper>
        <ProductImage src={"" + process.env.REACT_APP_ASSETS_URL + image?.url} alt="" />
      </ProductImageWrapper>
      <Price>
        Total {formatCurrency(useSelector(selectBuildCost))}
      </Price>
      <ButtonGroup>
        <Button secondary disabled={emailSent} onClick={sendMail}><VscMail/> {emailSent ? 'Sent' : 'Email Me a Copy'}</Button>
        <Button ><VscCreditCard/> <span>Checkout</span></Button>
      </ButtonGroup>

    </Wrapper>  )
}
