import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { Image } from './catalog'
import { address } from './customer'
import { apiCallBegan } from './middleware/api'
import { AppThunk, RootState } from './store'

export interface Company {
  id: string
  name: string
  address?: address
  phone?: string
  email?: string
  company_type: string
  logo_light?: Image
  logo_dark?: Image
  public_email?: string
  website_url?: string
  build_notifications_email?: string
  address_line_1?: string
  address_line_2?: string
  city: string
  country: string
  state: string
  postcode: string
  price_modification?: [
    {
      name: string
      value: string
      models: string[]
    },
  ]
}

interface CompanyStore {
  companies: Company[]
  loading: boolean
}
const initialState: CompanyStore = {
  companies: [],
  loading: false,
}

export const companySlice = createSlice({
  name: 'company',
  initialState,
  reducers: {
    companiesReceived: (state, action: PayloadAction<any>) => {
      state.companies = action.payload.dealersAndManufacturers
      state.loading = false
    },
    companiesRequested: (state) => {
      state.loading = true
    },
  },
})

export const loadCompanies = (): AppThunk => (dispatch) => {
  const graphQlQuery = /*GRAPHQL*/ `{
    dealersAndManufacturers {
      data {
        id
        attributes{
          name
          address_line_1
          address_line_2
          phone
          city
          state
          country
          postcode
          email
          website_url
          company_type
          build_notifications_email
          price_modification {
            id
            name
            value
            models {
              data {
                id
              }
            }
          }
        }
      }
    }
  }`

  return dispatch(
    apiCallBegan({
      url: process.env.REACT_APP_GRAPHQL_URL + '',
      graphQlQuery,
      onStart: companySlice.actions.companiesRequested.type,
      onSuccess: companySlice.actions.companiesReceived.type,
    }),
  )
}

export const { companiesReceived, companiesRequested } = companySlice.actions

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.counter.value)`
// export const selectBuilderStep = (state: RootState) => state.ui.builderStep
export const selectCompanyDetails = (state: RootState) => {
  return state.companies.companies
}
export const selectCompanyById = (id: string) => (state: RootState) => {
  return state.companies.companies.find((company) => company.id === id)
}
export default companySlice.reducer
