import { createEntityAdapter, createSlice } from '@reduxjs/toolkit'

export interface CustomOption {
  id: string
  option_name: string
  option_description?: string
  rrp?: number
  option_image?: Image[]
}
export interface CustomOptionPicker {
  id: string
  screen_name: string
  option_name: string
  options: CustomOption[]
}

export interface Category {
  id: string
  name: string
  icon: {
    url: string
  }
  accessories: Accessory[]
}
export interface Image {
  url: string
  id: string
  width: number
  height: number
  alternativeText: string
}
export interface buildItem {
  id: string
  name: string
  short_description: string
  description?: string
  rrp?: number
  featured_image?: Image
  slideshow_images?: Image[]
}

export interface Product extends buildItem {
  models: Model[]
}

export interface Accessory extends buildItem {
  option_for: [
    {
      id: string
    },
  ]
  included_in: [
    {
      id: string
    },
  ]
  accessory_categories: [
    {
      id: string
      icon: {
        url: string
      }
    },
  ]
}

interface Model extends buildItem {
  included_accessories: [
    {
      id: string
    },
  ]
  optional_accessories: [
    {
      id: string
    },
  ]
}

export const accessoriesAdapter = createEntityAdapter<Accessory>({
  // Assume IDs are stored in a field other than `accessory.id`
  selectId: (accessory) =>
    // console.log('acc', accessory);
    accessory.id,
  // Keep the "all IDs" array sorted based on accessory titles
  sortComparer: (a, b) => a.name.localeCompare(b.name),
})

const accessoriesSlice = createSlice({
  name: 'accessories',
  initialState: accessoriesAdapter.getInitialState(),
  reducers: {
    // Can pass adapter functions directly as case reducers.  Because we're passing this
    // as a value, `createSlice` will auto-generate the `accessoryAdded` action type / creator
    accessoryAdded: accessoriesAdapter.addOne,
    accessoriesReceived(state, action) {
      // Or, call them as "mutating" helpers in a case reducer
      accessoriesAdapter.setMany(state, action.payload.accessories)
    },
  },
})

export const accessoriesReducer = accessoriesSlice.reducer

