import { Action, configureStore, ThunkAction } from '@reduxjs/toolkit'
import buildReducer from './build'
import catalogReducer, { loadAccessories, loadCatalog } from './catalog'
import companiesReducer, { loadCompanies } from './companies'
import customerReducer from './customer'
import { accessoriesReducer } from './entities'
import api from './middleware/api'
import { strapiApi } from './middleware/strapiApi'
import settingsReducer from './settings'
import uiReducer from './ui'

export const store = configureStore({
  reducer: {
    catalog: catalogReducer,
    build: buildReducer,
    ui: uiReducer,
    settings: settingsReducer,
    customer: customerReducer,
    companies: companiesReducer,
    accessories: accessoriesReducer,
    [strapiApi.reducerPath]: strapiApi.reducer,
  },
  middleware: (getDefaultMiddleware) => getDefaultMiddleware().concat(api, strapiApi.middleware),
})

export type AppDispatch = typeof store.dispatch

// cast to allow for dispatching thunkactions
store.dispatch(loadCatalog())
// a.then( (b, c) => console.log('b', b, c))
store.dispatch(loadAccessories())
store.dispatch(loadCompanies())
// store.dispatch(loadCategories())
// store.dispatch<any>(loadSettings())
// store.dispatch<any>(loadCustomOptionPickers(1))
export type RootState = ReturnType<typeof store.getState>
export type AppThunk<ReturnType = void> = ThunkAction<ReturnType, RootState, unknown, Action<string>>
