import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { RootState } from './store'

export const UIScreenTitles = {
  SELECT_PRODUCT: 'Select Product',
  SELECT_MODEL: 'Select Model',
  YOUR_DETAILS: 'Your Details',
  ADD_ACCESSORIES: 'Add Accessories',
  DELIVERY_DETAILS: 'Delivery Details',
  YOUR_ADDRESS: 'Your Address',
  CONFIRMATION: 'Confirmation',
  PRODUCT_DETAILS: 'Product Details',
  MODEL_DETAILS: 'Model Details',
  SELECT_ACCESSORIES: 'Add Accessories List',
  ACCESSORY_DETAILS: 'Accessory Details',
}

interface uiState {
  builderStep: number
  uiCurrentScreen: string
  uiScreenOrder: string[]
  uiDisplayingDetailsForId: string
}

const screenOrder = window.szReactPlugin?.require_user_contact_details_upfront
  ? [
      UIScreenTitles.SELECT_PRODUCT,
      UIScreenTitles.YOUR_DETAILS,
      UIScreenTitles.SELECT_MODEL,
      UIScreenTitles.ADD_ACCESSORIES,
      UIScreenTitles.DELIVERY_DETAILS,
      UIScreenTitles.YOUR_ADDRESS,
      UIScreenTitles.CONFIRMATION,
    ]
  : [
      UIScreenTitles.SELECT_PRODUCT,
      UIScreenTitles.SELECT_MODEL,
      UIScreenTitles.ADD_ACCESSORIES,
      UIScreenTitles.YOUR_DETAILS,
      UIScreenTitles.DELIVERY_DETAILS,
      UIScreenTitles.YOUR_ADDRESS,
      UIScreenTitles.CONFIRMATION,
    ]

const initialState: uiState = {
  builderStep: 0,
  uiCurrentScreen: UIScreenTitles.SELECT_PRODUCT,
  uiScreenOrder: screenOrder,
  uiDisplayingDetailsForId: '0',
}

export const uiSlice = createSlice({
  name: 'ui',
  initialState,
  reducers: {
    nextBuilderStep: (state) => {
      switch (state.uiCurrentScreen) {
        case UIScreenTitles.CONFIRMATION:
          break
        case UIScreenTitles.PRODUCT_DETAILS:
        case UIScreenTitles.MODEL_DETAILS:
        case UIScreenTitles.ACCESSORY_DETAILS:
        case UIScreenTitles.SELECT_ACCESSORIES:
          state.uiCurrentScreen = state.uiScreenOrder[state.builderStep]
          break
        default:
          state.builderStep++
          state.uiCurrentScreen = state.uiScreenOrder[state.builderStep]
          break
      }
    },
    prevBuilderStep: (state) => {
      if (state.builderStep > 0) {
        state.builderStep--
        state.uiCurrentScreen = state.uiScreenOrder[state.builderStep]
      }
    },
    setBuilderStep: (state, action: PayloadAction<number>) => {
      state.builderStep = action.payload
      state.uiCurrentScreen = state.uiScreenOrder[state.builderStep]
    },
    showModelDetailsScreen: (state, action: PayloadAction<string>) => {
      state.uiCurrentScreen = UIScreenTitles.MODEL_DETAILS
      state.uiDisplayingDetailsForId = action.payload
    },
    showProductDetailsScreen: (state, action: PayloadAction<string>) => {
      state.uiCurrentScreen = UIScreenTitles.PRODUCT_DETAILS
      state.uiDisplayingDetailsForId = action.payload
    },
    closeProductDetailsScreen: (state) => {
      state.uiCurrentScreen = state.uiScreenOrder[state.builderStep]
    },
    closeModelDetailsScreen: (state) => {
      state.uiCurrentScreen = state.uiScreenOrder[state.builderStep]
    },
    openSelectAccessoriesScreen: (state) => {
      state.uiCurrentScreen = UIScreenTitles.SELECT_ACCESSORIES
    },
    closeSelectAccessoriesScreen: (state) => {
      state.uiCurrentScreen = state.uiScreenOrder[state.builderStep]
    },
    showAccessoryDetailsScreen: (state, action: PayloadAction<string>) => {
      state.uiDisplayingDetailsForId = action.payload
      state.uiCurrentScreen = UIScreenTitles.ACCESSORY_DETAILS
    },
    closeAccessoryDetailsScreen: (state) => {
      state.uiCurrentScreen = state.uiScreenOrder[state.builderStep]
    },
  },
})

export const {
  nextBuilderStep,
  prevBuilderStep,
  setBuilderStep,
  showModelDetailsScreen,
  showProductDetailsScreen,
  closeModelDetailsScreen,
  closeProductDetailsScreen,
  openSelectAccessoriesScreen,
  closeSelectAccessoriesScreen,
  showAccessoryDetailsScreen,
  closeAccessoryDetailsScreen,
} = uiSlice.actions

export const selectCurrentStep = (state: RootState) => state.ui.builderStep

export const selectCurrentUiScreen = (state: RootState) => state.ui.uiCurrentScreen
export const selectDisplayedId = (state: RootState) => state.ui.uiDisplayingDetailsForId
export const selectScreenOrder = (state: RootState) => state.ui.uiScreenOrder
export const selectNextButtonLabel = (state: RootState) => {
  if (
    state.ui.uiCurrentScreen === UIScreenTitles.SELECT_ACCESSORIES ||
    state.ui.uiCurrentScreen === UIScreenTitles.MODEL_DETAILS ||
    state.ui.uiCurrentScreen === UIScreenTitles.PRODUCT_DETAILS ||
    state.ui.uiCurrentScreen === UIScreenTitles.ACCESSORY_DETAILS
  ) {
    return 'Done'
  }
  return state.ui.uiScreenOrder[state.ui.builderStep + 1]
}

export default uiSlice.reducer
