import React from "react"
import { useSelector } from "react-redux"
import { selectAccessoryById } from "../../../store/catalog"
import { selectDisplayedId } from "../../../store/ui"
import { Heading3, Heading4 } from "../../common/style"
import { Col, DetailsWrapper } from "./style"

export default function AccessoryDetails() {
  const id = useSelector(selectDisplayedId);
  
  const accessory = useSelector(selectAccessoryById(id))

  return (
    <DetailsWrapper>
      <Col>
        {accessory && <img src={accessory.featured_image && process.env.REACT_APP_ASSETS_URL + accessory.featured_image.url} alt="" />}
      </Col>
      <Col>
        <Heading3>{accessory?.name}</Heading3>
        {accessory?.rrp && <Heading4>${accessory?.rrp}</Heading4>}
        {accessory?.description}
      </Col>
      
    </DetailsWrapper>
  )
}
