import React, { useEffect } from "react"
import { useForm } from "react-hook-form"
import { useDispatch } from "react-redux"
import { customerDetails, updateCustomerDetails } from "../../../store/customer"
import { UIScreenTitles } from "../../../store/ui"
import { Checkbox, ErrorMessage, FieldSet, Form, Input, Label, LabelText, Legend } from '../../common/forms'

interface props {
  onValidChange: (isValid: boolean, screen: string) => void
}

export default function ContactDetailsForm(props: props) {
  const dispatch = useDispatch()
  const { register, errors, getValues, triggerValidation } = useForm<
    customerDetails
  >({
    mode: "onChange",
  })

  useEffect(() => {
    props.onValidChange(false, UIScreenTitles.YOUR_DETAILS)
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  
  const handleChange = async () => {
    dispatch(updateCustomerDetails(getValues()))
    const result = await triggerValidation()

    props.onValidChange(result, UIScreenTitles.YOUR_DETAILS)
  }

  return (
    
    <Form onChange={handleChange} className="szForm">
      <FieldSet>
        <Legend>Enter your contact details</Legend>
        <Label htmlFor="firstName" >
          <LabelText>First Name</LabelText>
          <Input 
            id="firstName"
            name="firstName"
            placeholder="John"
            ref={register({ required: true })}
          />
        </Label>

        <Label htmlFor="surname" >
          <LabelText>Surname</LabelText>
          <Input 
            id="surname"
            name="surname"
            placeholder="Smith"
            ref={register({ required: true })}
          />
        </Label>

        <Label htmlFor="phone">
          <LabelText>Phone</LabelText>
          <Input
            id="phone"
            name="phone"
            placeholder="0400 000 000"
            ref={register({ required: true })}
          />
        </Label>

        <Label htmlFor="email">
          <LabelText>Email</LabelText>
          <Input
            id="email"
            name="email"
            placeholder="your@email.com"
            ref={register({
              required: true,
              pattern: {
                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                message: "Invalid email address",
              },
            })}
          />
        </Label>
        {errors.email && <ErrorMessage>{errors.email.message}</ErrorMessage>}

        <Label htmlFor="postcode">
          <LabelText>Postcode</LabelText>
          <Input
            id="postcode"
            name="postcode"
            placeholder="3000"
            ref={register({ required: true })}
          />
        </Label>

        <Label transparent htmlFor="newsletter" >
          <Checkbox
            id="newsletter"
            type="checkbox"
            name="newsletter"
            defaultChecked={true}
            ref={register}
          />
            Sign up for special offers and promotions
        </Label>
      </FieldSet>
    </Form>
    
  )
}
