import React from 'react'
import { VscAdd } from 'react-icons/vsc'
import { useDispatch, useSelector } from 'react-redux'
import { animated, config, useTransition } from 'react-spring'
import {
  addAccessory,
  removeAccessory,
  selectBuildAccessories,
  selectBuildIncludedAccessories,
} from '../../../store/build'
import { openSelectAccessoriesScreen } from '../../../store/ui'
import { formatCurrency } from '../../../utils/utils'
import { Heading2 } from '../../common/style'
import Accordion, { AccordionItem } from '../Accordion'
import MiniCard from '../MiniCard'
import { AddButton, Header, Wrapper } from './style'

interface AccessoriesListProps {
  onDetailsClick: (id: string) => void
}

export default function BuildAccessoriesList(props: AccessoriesListProps) {
  const buildAccessories = useSelector(selectBuildAccessories)
  const includedAccessories = useSelector(selectBuildIncludedAccessories)

  const dispatch = useDispatch()
  const transition = useTransition(buildAccessories, {
    from: { transform: 'translateX(10%)', opacity: 0 },
    enter: { opacity: 1, maxHeight: '63px', transform: 'translateX(0%)' },
    leave: { opacity: 0, maxHeight: '0px', transform: 'translateX(-0%)' },
    config: config.stiff,
    // delay: 1000,
  })

  const optionalAccessoriesList = transition(
    (style, accessory) =>
      accessory && (
        <animated.div style={style}>
          <MiniCard
            key={accessory.id}
            image={accessory.featured_image && process.env.REACT_APP_ASSETS_URL + accessory.featured_image.url}
            onDetailsClick={() => props.onDetailsClick(accessory.id)}
            onAddClick={() => dispatch(addAccessory(accessory.id))}
            onRemoveClick={() => dispatch(removeAccessory(accessory.id))}
            title={accessory.name}
            description={formatCurrency(accessory.rrp || 0)}
            isAdded
            showAdd
          />
        </animated.div>
      ),
  )
  const includedAccessoriesList = includedAccessories?.map(
    (accessory) =>
      accessory && (
        <MiniCard
          key={accessory.id}
          image={accessory.featured_image && process.env.REACT_APP_ASSETS_URL + accessory.featured_image.url}
          onDetailsClick={() => props.onDetailsClick(accessory.id)}
          onAddClick={() => dispatch(addAccessory(accessory.id))}
          onRemoveClick={() => dispatch(removeAccessory(accessory.id))}
          title={accessory.name}
          description={accessory.rrp && accessory.rrp !== 0 ? formatCurrency(accessory.rrp) : undefined}
          isAdded
          isCompact
        />
      ),
  )

  return (
    <Wrapper>
      <Header>
        <Heading2>Your Accessories</Heading2>
      </Header>
      <Accordion>
        <AccordionItem title={`Optional Accessories (${buildAccessories.length})`}>
          <AddButton onClick={() => dispatch(openSelectAccessoriesScreen())}>
            <VscAdd /> Add Accessories
          </AddButton>
          {optionalAccessoriesList}
        </AccordionItem>
        <AccordionItem title={`Included Accessories (${includedAccessories?.length})`}>
          {includedAccessoriesList}
        </AccordionItem>
      </Accordion>
    </Wrapper>
  )
}
