import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { selectBuildProduct, setProduct } from '../../../store/build'
import { selectProducts } from '../../../store/catalog'
import { showProductDetailsScreen, UIScreenTitles } from '../../../store/ui'
import { formatCurrency } from '../../../utils/utils'
import { Heading3, Heading4, TextButton } from '../../common/style'
import {
  ProductDetails,
  ProductFooter,
  ProductListing,
  ProductThumb,
  ProductThumbWrapper,
  ProductWrapper,
  ShortDescription,
} from './style'

interface props {
  onValidChange: (isValid: boolean, screen: string) => void
}

export default function ProductPicker(props: props) {
  const dispatch = useDispatch()
  const products = useSelector(selectProducts)
  const product = useSelector(selectBuildProduct)

  //if one of these products is selected, allow progress
  useEffect(() => {
    if (products.find((prod) => prod.id === product?.id)) {
      props.onValidChange(true, UIScreenTitles.SELECT_PRODUCT)
    } else {
      props.onValidChange(false, UIScreenTitles.SELECT_PRODUCT)
    }
  }, [product, products, props])

  let buildItemsMarkup
  if (products) {
    buildItemsMarkup = products.map((x) => {
      return (
        <ProductWrapper
          $isSelected={product?.id === x.id}
          key={x.id}
          onClick={() => {
            dispatch(setProduct(x.id))
          }}>
          <ProductThumbWrapper>
            {x.featured_image?.url && <ProductThumb alt={x.name} src={x.featured_image?.url} />}
          </ProductThumbWrapper>
          <ProductDetails>
            <Heading3>{x.name}</Heading3>
            <ShortDescription>{x.short_description}</ShortDescription>
            <ProductFooter>
              {x.rrp && <Heading4>Starts at {formatCurrency(x.rrp)}</Heading4>}
              <TextButton onClick={() => dispatch(showProductDetailsScreen(x.id))}>View Specs</TextButton>
            </ProductFooter>
          </ProductDetails>
        </ProductWrapper>
      )
    })
  }

  return <ProductListing>{buildItemsMarkup}</ProductListing>
}
