import React, { useEffect, useState } from "react"
import { useForm } from "react-hook-form"
import { useDispatch, useSelector } from "react-redux"
import { selectCompanyById, selectCompanyDetails } from "../../../store/companies"
import { deliveryDetails, updateDeliveryDetails } from "../../../store/customer"
import { UIScreenTitles } from "../../../store/ui"
import { FieldSet, Form, Label, LabelText, Legend, Radio, Select } from "../../common/forms"

interface props {
  onValidChange: (isValid: boolean, screen: string) => void
}

export default function DeliveryDetailsForm(props: props) {
  const dispatch = useDispatch()
  const companies = useSelector(selectCompanyDetails);
  
  console.log(companies)
  const { register, triggerValidation, getValues } = useForm<deliveryDetails>({
    mode: "onChange",
    reValidateMode: "onChange",
  })
  const [selectedCompanyId, setselectedCompanyId] = useState(companies[0].id)
  const selectedCompany = useSelector(selectCompanyById(selectedCompanyId))

  useEffect(() => {
    props.onValidChange(false, UIScreenTitles.DELIVERY_DETAILS)
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  

  const handleChange = async () => {
    dispatch(updateDeliveryDetails(getValues()))
    setselectedCompanyId(getValues().dealer)
    const result = await triggerValidation()
    props.onValidChange(result, UIScreenTitles.DELIVERY_DETAILS)
  }

  return (
    <React.Fragment>
      <Form onChange={handleChange} >
        <FieldSet>
          <Legend>Delivery Method</Legend>
          <Label transparent htmlFor="delivery" >
            <Radio
              id="delivery"
              name="deliveryMethod"
              value="delivery"
              ref={register}
            />
            <LabelText>Delivery</LabelText>
          </Label>

          <Label transparent htmlFor="pickup" >
            <Radio
              id="pickup"
              name="deliveryMethod"
              value="pickup"
              ref={register({
                validate: () => {
                  console.log(getValues())
                  return getValues().deliveryMethod !== ""
                },
              })}
            />
            <LabelText>Pick Up</LabelText>
          </Label>
        </FieldSet>

        <FieldSet>
          <Legend>Choose your dealer</Legend>
          <Select name="dealer" id="dealer" defaultValue={companies[0].id} ref={register}>
            {companies.map((company, idx) => <option value={company?.id} key={idx}>{company?.state} - {company?.name} - {company?.city}</option>)}
          </Select>
        </FieldSet>
      </Form>
      {selectedCompany && <div className="dealer-details-display">
        <h3 className="dealer-name">{selectedCompany?.name}</h3>
        <span className="dealer-phone">{selectedCompany?.address_line_1}, </span>
        {selectedCompany?.address_line_2 && <span className="dealer-address2">{selectedCompany?.address_line_2}</span>}
        <span className="dealer-city">{selectedCompany?.city}</span><br/>
        <span className="dealer-state">{selectedCompany?.state}</span><br/>
        <span className="dealer-country">{selectedCompany?.country}</span><br/>
        {selectedCompany?.phone && <span className="dealer-phone">Phone: {selectedCompany?.phone}<br/></span>}
        <span className="dealer-website"><a href={selectedCompany?.website_url} target="_blank" rel='noopener noreferrer' >Visit Website</a></span>
      </div>}
    </React.Fragment>
  )
}
