import React from "react"
import ReactMarkdown from "react-markdown"
import { useDispatch, useSelector } from "react-redux"
import { selectProductById } from "../../../store/catalog"
import { selectDisplayedId } from "../../../store/ui"
import { formatCurrency } from "../../../utils/utils"
import { Screen } from "../../common/style"
import { Price, ProductImage, ProductInfo } from "./style"
export default function ProductDetails() {

  const id = useSelector(selectDisplayedId)
  const product = useSelector(selectProductById(id))
  const dispatch = useDispatch()

  
  return (
    <Screen>
      {product && 
        <ProductImage src={"" + process.env.REACT_APP_ASSETS_URL + product?.featured_image?.url} alt="" />}
      <ProductInfo>
        <h3>{product && product?.name}</h3>
        <Price>{product?.rrp && formatCurrency(product.rrp)}</Price>
        <ReactMarkdown>{product?.description +""}</ReactMarkdown>
      </ProductInfo>
    </Screen>
  )
}
