import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { config, useTransition } from 'react-spring'
import { callWebhook, selectFullBuild } from '../store/build'
import {
  nextBuilderStep,
  prevBuilderStep,
  selectCurrentStep,
  selectCurrentUiScreen,
  selectNextButtonLabel,
  selectScreenOrder,
  UIScreenTitles,
} from '../store/ui'
import './App.css'
import AddressForm from './components/AddressForm/AddressForm'
import BuildStatusBox from './components/BuildStatusBox'
import ContactDetailsForm from './components/ContactDetailsForm/ContactDetailsForm'
import DeliveryDetailsForm from './components/DeliveryDetailsForm/DeliveryDetailsForm'
import FooterNav from './components/FooterNav'
import ProductDetails from './components/ProductDetails'
import AccessoriesPicker from './components/ScreenAccessoriesPicker/AccessoriesPicker'
import AccessoryDetails from './components/ScreenAccessoryDetails'
import ModelDetails from './components/ScreenModelDetails'
import ModelPicker from './components/ScreenModelPicker/ModelPicker'
import ProductPicker from './components/ScreenProductPicker'
import SelectAccessories from './components/ScreenSelectAccessories'
import ThankYouScreen from './components/ScreenThankYou'
import StepTitles from './components/StepTitles/StepTitles'
import { AnimatedScreen, AppHeader, AppWrapper, Container, ScreenSection } from './style'

declare global {
  interface Window {
    szReactPlugin: {
      webhook_url: string
      accent_color: string
      email_endpoint: string
      require_user_contact_details_upfront: boolean
    }
  }
}

if (window.szReactPlugin) {
  document.documentElement.style.setProperty('--accent-dark', window.szReactPlugin.accent_color)
}

function App() {
  const currentScreen = useSelector(selectCurrentUiScreen)
  const currentScreenIdx = useSelector(selectCurrentStep)
  const screenNames = useSelector(selectScreenOrder)
  const fullBuild = useSelector(selectFullBuild)
  const nextLabel = useSelector(selectNextButtonLabel)
  const dispatch = useDispatch()
  const transition = useTransition(currentScreen, {
    from: { transform: 'translateX(10%)', opacity: 0 },
    enter: { opacity: 1, transform: 'translateX(0%)' },
    leave: { opacity: 0, transform: 'translateX(-0%)' },
    config: config.slow,
  })

  const [canAdvance, setCanAdvance] = useState(true)

  const handleValidChange = (isValid: boolean, screen: string) => {
    if (screen === currentScreen) {
      setCanAdvance(isValid)
    }
  }

  const handleNextClick = () => {
    if (
      (currentScreen === UIScreenTitles.YOUR_DETAILS || currentScreen === UIScreenTitles.YOUR_ADDRESS) &&
      window.szReactPlugin?.webhook_url
    ) {
      dispatch(callWebhook(window.szReactPlugin?.webhook_url, fullBuild))
    }
    dispatch(nextBuilderStep())
    setCanAdvance(true)
  }

  const getScreen = (screen: string) => {
    switch (screen) {
      case UIScreenTitles.SELECT_PRODUCT:
        return <ProductPicker onValidChange={handleValidChange} />
      case UIScreenTitles.SELECT_MODEL:
        return <ModelPicker onValidChange={handleValidChange} />
      case UIScreenTitles.ADD_ACCESSORIES:
        return <AccessoriesPicker />
      case UIScreenTitles.YOUR_DETAILS:
        return <ContactDetailsForm onValidChange={handleValidChange} />
      case UIScreenTitles.DELIVERY_DETAILS:
        return <DeliveryDetailsForm onValidChange={handleValidChange} />
      case UIScreenTitles.YOUR_ADDRESS:
        return <AddressForm onValidChange={handleValidChange} />
      case UIScreenTitles.CONFIRMATION:
        return <ThankYouScreen />
      case UIScreenTitles.MODEL_DETAILS:
        return <ModelDetails />
      case UIScreenTitles.PRODUCT_DETAILS:
        return <ProductDetails />
      case UIScreenTitles.SELECT_ACCESSORIES:
        return <SelectAccessories />
      case UIScreenTitles.ACCESSORY_DETAILS:
        return <AccessoryDetails />
    }
  }

  return (
    <AppWrapper>
      <Container>
        <AppHeader>
          <StepTitles titles={screenNames} activeStep={screenNames[currentScreenIdx]} />
          <BuildStatusBox />
        </AppHeader>
        <ScreenSection>
          {transition((style, theCurrentScreen, t) => (
            <AnimatedScreen style={style} $isActive={theCurrentScreen === currentScreen}>
              {getScreen(theCurrentScreen)}
            </AnimatedScreen>
          ))}
        </ScreenSection>
        <FooterNav
          onNextClick={handleNextClick}
          onPrevClick={() => {
            dispatch(prevBuilderStep())
            setCanAdvance(true)
          }}
          showNext={currentScreenIdx < screenNames.length - 1}
          showPrev={true}
          prevEnabled={currentScreenIdx !== 0}
          nextEnabled={canAdvance}
          nextLabel={nextLabel}
          prevLabel="Back"
        />
      </Container>
    </AppWrapper>
  )
}

export default App
