import styled from 'styled-components/macro'

interface labelProps {
  transparent?: boolean
}

export const Form = styled.form`
  padding-top: 32px;
  margin-bottom: 32px;
  box-sizing: border-box;
`

export const Input = styled.input`
  border: 0;
  padding: 14px 10px;
  flex-grow: 1;

  &:active,
  &:focus {
    outline: none;
  }
`
export const Radio = styled.input.attrs({ type: 'radio' })`
  border: 0;
  padding: 14px 10px;
  flex-grow: 0;
  display: block;
  &:active,
  &:focus {
    outline: none;
  }
`

export const TextArea = styled.textarea`
  &:focus {
    outline: none;
  }
`

export const Checkbox = styled.input.attrs({ type: 'checkbox' })`
  width: 20px;
  flex-grow: 0;
`

export const LabelText = styled.span`
  width: 100px;
  display: inline-block;
`

export const Label = styled.label<labelProps>`
  margin-bottom: 16px;
  background-color: ${(p) => (p.transparent ? 'transparent' : 'white')};
  box-shadow: ${(p) => (p.transparent ? 'none' : '0px 2px 10px rgba(0, 0, 0, 0.02);')};
  padding: ${(p) => (p.transparent ? '0' : '0 0 0 12px;')};
  display: flex;
  align-items: center;

  &:focus-within {
    box-shadow: ${(p) =>
      p.transparent ? 'none' : '0px 2px 10px rgba(0, 0, 0, 0.02), 0 0 3px var(--input-outline-hover)'};
  }
`

export const FieldSet = styled.fieldset`
  border: 0;
  padding: 0;
`

export const Select = styled.select`
  padding: 14px 10px;
  border: 0;
  width: 100%;
`

export const ErrorMessage = styled.span``

export const Legend = styled.legend`
  font-weight: 700;
  margin-bottom: 0.8rem;
`
