import styled from 'styled-components/macro'

interface theProps {
  isActive?: boolean
}

export const Wrapper = styled.div<theProps>`
  border: 1px solid var(--grey-2);
  display: none;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  padding: 4px;
  box-sizing: border-box;
  min-width: 320px;

  @media (min-width: 1000px){
    display: grid;
  }

`


export const Icon = styled.div`
  font-size: 45px;
  margin-right: 6px;
  display: flex;
  align-items: center;

  svg{
    color: var(--grey-4);
    stroke-width: 1;
  }
`

export const InfoWrapper = styled.div`
    align-self: flex-start;
`

export const Title = styled.div`
  margin: 0;
  margin-top: 6px;
  font-size: 20px;
  color: var(--accent-dark);
  line-height: 20px;
  `

export const Price = styled.div`
  font-size: 20px;
  font-weight: 800;
  color: var(--grey-5);
`

export const Subtitle = styled.div`
  color: var(--grey-5);
  font-size: 15px;
`