import { animated } from "react-spring";
import styled, { css } from 'styled-components/macro';

/*
$ sign marks the prop as transient, so it isn't aded to dom. 
https://github.com/styled-components/styled-components/pull/3052
*/
interface theProps {
  $isActive?: boolean
}

export const TitleText = styled.h3`
  font-size:18px;
  letter-spacing: -0.53px;
  white-space: nowrap;
  line-height: 0 !important;
  margin: 0 !important;
  color:var(--grey-3) !important;
  font-weight: 300!important;
  /* height:0; */
  
`

export const Wrapper = styled( (animated.div) )<theProps>`
  position:absolute;
  border: ${p=>p.$isActive ? '2px solid var(--accent-dark-trans);' : '2px solid var(--grey-2);'};
  border-radius: 999px;
  display:flex;
  overflow: hidden;
  justify-content: center;
  will-change: transform, width;
  align-items:center;
  padding: 18px;
  box-sizing: border-box;
  height: ${p=> !p.$isActive ? '36px' : 'auto'};
  ${TitleText} { 
    ${p=> (p.$isActive && css`
      color: var(--accent-dark) !important;
      font-weight: bold!important;`
  )}  
  } 
`
