
import React from "react";
import { config, useSpring } from "react-spring";
import { TitleText, Wrapper } from "./style";

type StepTitleProps = {
  title: string
  isActive?: boolean
  number: number
}

export default function StepTitle(props: StepTitleProps) {

  const expand = useSpring({
    config: config.stiff,
    width: props.isActive ? `${220}px` : '36px'
  });
  const text = props.isActive ? props.number +". "+ props.title : props.number;
  return (
    <Wrapper style={expand} $isActive={props.isActive} >
      <TitleText as="h3" >{text}</TitleText>
    </Wrapper>

  )
}
