import { animated } from 'react-spring'
import styled from 'styled-components/macro'
/*
$ sign marks the prop as transient, so it isn't aded to dom. 
https://github.com/styled-components/styled-components/pull/3052
*/
interface theProps {
  $isSelected?: boolean
}

export const AnimatedProductWrapper = styled(animated.div)<theProps>`
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: white;
  border-width: 2px;
  border-style: solid;
  border-color: ${(p) => (p.$isSelected ? 'var(--accent-dark)' : 'transparent')};
  max-height: calc(2 * var(--product-select-height));
  padding: 25px;
  box-shadow: ${(p) => (p.$isSelected ? 'box-shadow: 0px 0px 8px var(--box-shadow)' : 'none')};
  transition: box-shadow 0.5s;
  box-sizing: border-box;
  cursor: pointer;
  &:hover {
    border: 2px solid black;
  }
`

export const ProductWrapper = styled.div<theProps>`
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: white;
  border-width: 2px;
  border-style: solid;
  border-color: ${(p) => (p.$isSelected ? 'var(--accent-dark)' : 'transparent')};
  max-height: calc(2 * var(--product-select-height));
  padding: 25px;
  box-shadow: ${(p) => (p.$isSelected ? 'box-shadow: 0px 0px 8px var(--box-shadow)' : 'none')};
  transition: box-shadow 0.5s;
  box-sizing: border-box;
  cursor: pointer;
  &:hover {
    border: 2px solid black;
  }
`

export const ProductListing = styled.div`
  display: grid;
  grid-template-columns: 1fr;
  column-gap: var(--product-select-gutter);
  row-gap: var(--product-select-gutter);
  height: 100%;
  grid-template-rows: repeat(auto-fit, minmax(0, 1fr));

  @media ${(p) => p.theme.device.mobileL} {
    grid-template-columns: repeat(auto-fit, minmax(40%, 1fr));
  }

  @media ${(p) => p.theme.device.tablet} {
    grid-template-columns: repeat(auto-fit, minmax(30%, 1fr));
  }
`
export const ProductThumbWrapper = styled.div`
  overflow: hidden;
  min-height: 60px;
`
export const ProductThumb = styled.img`
  object-fit: contain;
  width: 100%;
  height: 100%;
`

export const ShortDescription = styled.p`
  &&& {
    margin: 5px 0 0;
  }
`
export const ProductFooter = styled.footer`
  margin-top: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
`
export const ProductDetails = styled.div`
  margin-top: 30px;
`
